import React, { useEffect } from 'react';
import '../styles/ShopPage.css';

const brands = ['Wszystko', 'Just Unique', 'Gucci', 'LaCoste', 'Nike', 'Puma'];
const accesories = ['Wszystko', 'Bluzki', 'Bluzy', 'Body', 'Dresy', 'Kamizelki', 'Kombinezony', 'Komplety', 'Koszule', 'Kurtki', 'Legginsy', 'Marynarki', 'Narzutki', 'Płaszcze', 'Spodnie', 'Spódnice', 'Sukienki', 'Swetry', 'Szorty', 'Tuniki', 'Obuwie', 'Akcesoria', 'Nowości', 'Promocje'];


const Search = () => {

    const brandsTab = brands.map(brand => (
        <option value={brand.toLowerCase}>{brand}</option>
    ))
    
    const accesoriesTab = accesories.map(accesory => (
        <li className='shop-li'>{accesory}</li>
    ));

    function addActive(){

    }

    useEffect(() => {
        const leftSide = document.querySelector('.left-side');

        
    })


    return ( 
        <div className='left-side'>
            <div className="shop-tag">
                <h3 className="shop-header">
                    Producent
                </h3>
            </div>

            <div className="shop-producer">
                <h4>
                    Wybierz producenta
                </h4>
                <select className='shop-combobox' name="brands">
                    {brandsTab}
                </select>
            </div>

            <div className="shop-tag">
                <h3 className="shop-header">
                    Menu
                </h3>
            </div>
            <div className="shop-menu">
                <ul className="shop-ul">
                    {accesoriesTab}
                </ul>
            </div>
        </div>
     );
}
 
export default Search;