import React, { useEffect } from "react";
import "../styles/TopContainer.css";

const TopContainer = () => {
  useEffect(() => {
    const logButton = document.querySelector("#log-box-button");
    const loginPostContainer = document.querySelector(".inner-login-post");
    const bottomContainer = document.querySelector(".bottom-container");

    let sizeLoginPost = loginPostContainer.clientHeight;

    function getSize() {
      sizeLoginPost = loginPostContainer.clientHeight;
    }

    function setTop() {
      if(window.innerWidth > 1024){
        if (loginPostContainer.classList.contains("active")) {
          loginPostContainer.style.top = "60px";
          bottomContainer.style.paddingTop = `${sizeLoginPost}px`;
          
        } else {
          loginPostContainer.style.top = `-${sizeLoginPost}px`;
          bottomContainer.style.paddingTop = "0px";
        }
      }else{
        if (loginPostContainer.classList.contains("active")) {
          loginPostContainer.style.top = "0px";
          bottomContainer.style.paddingTop = `${sizeLoginPost}px`;
          
        } else {
          loginPostContainer.style.top = `-${sizeLoginPost}px`;
          bottomContainer.style.paddingTop = "0px";
        }
      }

      
    }

    logButton.addEventListener("click", function () {
      loginPostContainer.classList.toggle("active");
      logButton.classList.toggle("active");
      setTop();
    });

    function init() {
      getSize();
      setTop();
    }
    init();
    window.addEventListener("resize", init);
  });

  return (
    <div className="top-container">
      <div className="login-container">
        <ul className="login-ul">
          <li className="login-li" id="log-box-button">
            Zaloguj się
          </li>
        </ul>
      </div>
      <div className="inner-login-post">
        <div className="login-post">
          <form>
            <h1 className="post-h1">Masz już istniejące konto?</h1>
            <h2 className="post-h2">Zaloguj się!</h2>
            <label for="log">Login</label>
            <input type="text" name="log" className="login-input" />
            <label for="pass">Hasło</label>
            <input type="password" name="pass" className="login-input" />
            <button type="submit" className="button-login">
              Zaloguj!
            </button>
          </form>

          <form>
            <h1 className="post-h1">Nie masz jeszcze konta?</h1>
            <h2 className="post-h2">Zarejestruj się!</h2>
            <label for="login">Login</label>
            <input type="text" name="login" className="login-input" />
            <label for="pass">Hasło</label>
            <input type="password" name="pass" className="login-input" />
            <label for="email">E-mail</label>
            <input type="email" name="email" className="login-input" />
            <button type="submit" className="button-login">
              Zarejestruj!
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default TopContainer;
